.header {
  display: flex;
  justify-content: space-between;
  background-color: #9e9e9e;
  align-items: center;
  padding: 0 50px;
}

.logo {
  width: 40px;
  border-radius: 50%;
  padding: 10px;
  margin: auto 0;
}

.listContainer {
}

.list {
  display: flex;
  list-style: none;
}

.list li a {
  padding: 15px;
  cursor: pointer;
  color: #fff;
  font-weight: bold;
  text-decoration: none;
}

.list li a:hover {
  color: #b4b4b4;
}
